/*--------------------------------------------------------------
# Custom Styles
--------------------------------------------------------------*/
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "cairo", sans-serif;
  color: red;
}

p,
li {
  font-family: "Noto Kufi Arabic", sans-serif;
}

h1 {
  font-size: 2.5rem;
}

h2 {
  font-size: 2rem;
}

h3 {
  font-size: 1.75rem;
}

h4 {
  font-size: 1.5rem;
}

h5 {
  font-size: 1.25rem;
}

h6 {
  font-size: 1rem;
}

/* 
 *
 Header style
 *
*/
.site {
  overflow: hidden;
  padding-block: 4rem;
}

.site-header {
  position: absolute;
  top: 0;
  width: 100%;
  display: flex;
  margin: 0 auto;
}

.site-branding {
  font-family: "cairo", sans-serif;
  margin: auto 1.6rem;
  float: left;
  display: flex;
}

.site-branding a {
  text-decoration: none;
}

.site-title {
  font-family: "cairo", sans-serif;
  font-size: 2rem;
}

.site-title,
.site-description {
  white-space: nowrap;
  flex-direction: column;
  margin: 0.5rem;
}

.site-description {
  float: right;
  padding: 0.5rem;
  font-style: italic;
  vertical-align: bottom;
}

.menu-toggle {
  display: none;
}

.main-navigation {
  margin: auto 1rem;
  float: left;
  text-align: end;
  width: 100%;
}

.main-navigation .menu,
.main-navigation .sub-menu {
  padding: 0;
  margin: 0;
}

.main-navigation #primary-menu {
  display: inline-block;
  align-items: center;
  width: 100%;
}

.main-navigation ul ul {
  z-index: 9999;
  position: absolute;
  width: 10rem;
  padding: 0;
  margin: 0;
}

.main-navigation ul ul ul {
  right: 10rem;
  left: auto;
  top: 0;
  padding: 0;
  margin: 0;
}

.main-navigation ul ul ul ul {
  right: 10rem;
  left: auto;
  top: 0;
  padding: 0;
  margin: 0;
}

.main-navigation ul ul ul ul ul {
  left: 0;
  right: 0;
  top: auto;
}

.main-navigation li {
  text-align: center;
  font-family: "cairo", sans-serif;
  font-size: 1.44rem;
  display: inline-block;
  position: relative;
  float: none;
  padding: 0.4rem;
  margin: 0 0.4rem;
}

.main-navigation li li {
  width: 10rem;
  margin: 0;
}

.main-navigation li a {
  display: block;
  margin: 0;
  padding: 0;
  width: 100%;
}

.main-navigation ul ul {
  visibility: hidden;
  opacity: 0;
}

.main-navigation ul li:hover > ul {
  visibility: visible;
  opacity: 1;
}

.header_image img {
  width: 100%;
  height: 100%;
}

