/* 
 *
 Site content style
 *
*/
body,
.page {
  margin: 0;
}

.site-main {
  background: #fff;
  width: 70%;
  float: left;
  padding: 2rem;
}

.widget-area {
  background: #fff;
  width: 30%;
  height: 100vh;
  float: right;
  padding: 2rem;
  padding-top: 3rem;
  overflow: hidden;
  word-break: normal;
  word-wrap: normal;
}

.no-sidebar .content-area {
  width: 100%;
  float: none;
  padding: 2rem;
}

.entry-header,
.entry-footer {
  text-align: center;
}

.entry-footer span {
  display: inline-block;
  padding: 1rem;
}

.entry-meta {
  margin-bottom: 1rem;
}

.post {
  word-break: break-word;
  padding: 1rem;
  border: 0.2rem solid #f2f2f2;
}

.post p,
.post pre,
.post ul,
.post ol,
.post code,
.post h1,
.post h2,
.post h3,
.post h4,
.post h5,
.post h6,
.post li,
.post em,
.post strong,
.post span {
  unicode-bidi: plaintext;
}

.post a {
  unicode-bidi: isolate;
}

.site-content {
  word-wrap: break-word;
}

.widget {
  padding: 1rem;
  border-bottom: 0.2rem solid #f2f2f2;
}

.widget ul {
  margin: 0;
  padding: 1rem;
  list-style: none;
}

.widget ul li {
  padding-top: 0.5rem;
}

.has-post-thumbnail {
  text-align: center;
}

.has-post-thumbnail p {
  text-align: left;
}

.entry-content figure {
  text-align: center;
}

blockquote {
  padding: 1rem;
  border: 0.2rem solid #f2f2f2;
}

code {
  background-color: #f2f2f2;
  unicode-bidi: plaintext;
}

.comment {
  padding: 0.5rem;
  line-height: normal;
  border: 0.2rem solid #f2f2f2;
  background-color: #f8f8ff;
  box-shadow: inset 0 0.2rem 0.5rem #f2f2f2;
}

.comment li > ol {
  margin: 0;
}

.format-image {
  text-align: center;
}

.widget ul {
  margin: 0.5rem;
}

.header-image img {
  width: 100%;
  height: 100%;
}

.nav-previous {
  float: left;
  width: 50%;
}

.nav-next {
  float: right;
  text-align: right;
  width: 50%;
}

/* 
 *
 footer style
 *
*/
.site-footer {
  width: 100%;
  text-align: center;
  margin-top: 1rem;
}

.site-info {
  padding: 0.5rem;
}

/* 
 *
 Site content responsiveness
 *
*/
@media (max-width: 1200px) {
  .content-area {
    padding: 1rem;
  }
  .widget-area {
    padding: 1rem;
    padding-top: 1.5rem;
  }
  .no-sidebar .content-area {
    padding: 1rem;
  }
  .entry-footer span {
    padding: 0.5rem;
  }
  .entry-meta {
    margin-bottom: 0.5rem;
  }
  .post {
    padding: 0.5rem;
  }
  .widget {
    padding: 0.5rem;
  }
  blockquote {
    padding: 0.5rem;
  }
  .comment {
    padding: 0.5rem;
  }
  ul,
  ol {
    margin: 0 1rem 0.5rem 0;
  }
  li > ol,
  li > ul {
    margin: auto;
  }
}

@media (max-width: 1024px) {
  .content-area {
    padding: 0.9rem;
  }
  .widget-area {
    padding: 0.9rem;
    padding-top: 1.3rem;
  }
  .no-sidebar .content-area {
    padding: 1rem;
  }
  .entry-footer span {
    padding: 0.4rem;
  }
  .entry-meta {
    margin-bottom: 0.4rem;
  }
  .post {
    padding: 0.4rem;
  }
  .widget {
    padding: 0.4rem;
  }
  blockquote {
    padding: 0.4rem;
  }
  .comment {
    padding: 0.4rem;
  }
  ul,
  ol {
    margin: 0 0.9rem 0.4rem 0;
  }
  li > ol,
  li > ul {
    margin: auto;
  }
}

@media (max-width: 950px) {
  .content-area h1,
  .widget-area h1 {
    font-size: 2.5rem;
  }
  .content-area h2,
  .widget-area h2 {
    font-size: 2rem;
  }
  .content-area h3,
  .widget-area h3 {
    font-size: 1.75rem;
  }
  .content-area h4,
  .widget-area h4 {
    font-size: 1.5rem;
  }
  .content-area h5,
  .widget-area h5 {
    font-size: 1.25rem;
  }
  .content-area h6,
  .widget-area h6 {
    font-size: 1rem;
  }
  .content-area {
    width: 100%;
    float: none;
    padding: 0.9rem;
  }
  .widget-area {
    width: 100%;
    float: none;
    text-align: left;
    padding: 0.9rem;
    padding-top: 1.3rem;
  }
  .site-footer {
    font-size: 0.8rem;
  }
  .comment {
    font-size: 0.8rem;
  }
  ul,
  ol {
    margin: 0 0.5rem 0.1rem 0;
  }
  li > ol,
  li > ul {
    margin: auto;
  }
}

@media (max-width: 750px) {
  .content-area h1,
  .widget-area h1 {
    font-size: 2rem;
  }
  .content-area h2,
  .widget-area h2 {
    font-size: 1.6rem;
  }
  .content-area h3,
  .widget-area h3 {
    font-size: 1.4rem;
  }
  .content-area h4,
  .widget-area h4 {
    font-size: 1.2rem;
  }
  .content-area h5,
  .widget-area h5 {
    font-size: 1rem;
  }
  .content-area h6,
  .widget-area h6 {
    font-size: 0.8rem;
  }
  .content-area {
    padding: 0.8rem;
  }
  .widget-area {
    padding: 0.8rem;
    padding-top: 1.2rem;
  }
  .site-footer {
    font-size: 0.64rem;
  }
  .comment {
    font-size: 0.64rem;
  }
  ul,
  ol {
    margin: 0 0.4rem 0.1rem 0;
  }
  li > ol,
  li > ul {
    margin: auto;
  }
}

@media (max-width: 460px) {
  .content-area h1,
  .widget-area h1 {
    font-size: 1.5rem;
  }
  .content-area h2,
  .widget-area h2 {
    font-size: 1.2rem;
  }
  .content-area h3,
  .widget-area h3 {
    font-size: 1.05rem;
  }
  .content-area h4,
  .widget-area h4 {
    font-size: 0.9rem;
  }
  .content-area h5,
  .widget-area h5 {
    font-size: 0.75rem;
  }
  .content-area h6,
  .widget-area h6 {
    font-size: 0.6rem;
  }
  .content-area {
    padding: 0.5rem;
    font-size: 0.8rem;
  }
  .widget-area {
    padding: 0.5rem;
    padding-top: 0.9rem;
    font-size: 0.8rem;
  }
  .entry-footer,
  .entry-meta {
    font-size: 0.64rem;
  }
  .entry-footer a,
  .entry-meta a {
    font-size: 0.64rem;
  }
  .site-footer {
    font-size: 0.64rem;
  }
  ol.comment-list {
    margin: 0;
    padding: 0;
  }
  .comment {
    font-size: 0.64rem;
  }
  .comment p {
    margin: 0;
  }
  .comment ul,
  .comment ol {
    margin: 0;
    padding: 0rem;
  }
  .comment li > ol,
  .comment li > ul {
    margin: 0;
  }
  .widget ul {
    margin: 0;
  }
}

